@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&family=Poppins:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #0059ff;
}
#quote-box{
  background: #fff;
  width: 40rem;
  margin: 10rem auto;
  border-radius: 5px;
  padding: 3rem;
  flex-wrap: wrap;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.9);
}
#text{
   font-size: 2rem;
   margin-bottom: 1.5rem;
   font-family: 'Poppins', sans-serif;
}
#author{
  display: flex;
  justify-content: flex-start;
  font-size: 1.2rem;
  font-family: 'Dancing Script', cursive;
  font-weight: bold;
  
}
#new{
  margin-top: 2rem;
  background: #0059ff;
  border: none;
  color: #fff;
  padding: .7rem 1rem;
  font-size: .9rem;
  font-weight: bold;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
  
}
#new:hover{
  opacity: 0.9;
}

.load{
  margin: 18rem;
text-align: center;

}
@media(max-width:600px){
  #quote-box{
    width: 25rem;
  }
}
@media(max-width:400px){
  #quote-box{
    width: 20rem;
  }
  .load{
    margin: 5rem;

  }
}
